@import './css/cancel';
body{
    background:whitesmoke;
    color: #333;
    overflow: hidden;
}

.app{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    header{
        font-size: 38px;
        flex: 1;
        padding: 50px 0 30px 0;
    }
    .visual{
        padding: 25px;
    }
    .pads{
        flex: 0;
        height: 30vh;
        display: flex;
        width: 100%;
        &>div{
            flex:1 1;
            cursor: pointer;
            box-shadow: inset 0.1px 0.1px 1px  gray;
            transition: 0.17s; 
            position: relative; 
            z-index: 1111;
            &:hover,&.active{
                transform: scale(1.05);  
            }  
        }
        .bubble{
           position: absolute;
           width: 50px; 
           height: 50px;
           border-radius: 50%;
           border: 1px solid;
           z-index: -1;
           left:50%;
           transform: translateX(-50%);
           animation: jump 2s ease forwards;
        }
    }
}

@keyframes jump{
    50%{
        transform: translateX(200%) translateY(-200px);
    }

    100%{
        transform: translateX(400%) translateY(0);
        opacity: 0;
    }
}

